.about{
    background-color: #FFF5F6;
    padding-top: 60px;
}


.about__label{
    text-transform: uppercase;
    line-height: 1.17;
    letter-spacing: 0.04em;
    margin-bottom:  15px;

    @media screen and(min-width: 768px){
        margin-bottom:  16px;
        line-height: 1.15;
    }
    @media screen and(min-width: 1200px){
        line-height:1.13;
    }
}

.about__title{
    line-height:1.15;
    letter-spacing: 0.04em;

    @media screen and(min-width: 1200px){
        line-height:1.14;
        letter-spacing: 0.06em;
    } 
}  


.about__label,
.about__title{
    text-align: center;
}

.about__title{
    margin-bottom: 32px;

    @media screen and(min-width: 768px){
        margin-bottom: 40px;
    }
    @media screen and(min-width: 1200px){
        margin-bottom: 60px;
    }
}

.about__details{
    position: relative;

    @media screen and(min-width: 768px){
        display: flex;
        align-items:flex-start;
       
    }
    @media screen and (min-width: 1200px){
        align-items: center;
    }
}

.overlay{

    @media screen and(min-width: 768px){
        background-image:none;
        }
        
    @media screen and (min-width: 1200px){
        margin-left: auto;
        margin-right: auto;
        padding: 0;
        width: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-image: url(../image/3-about/about-bg_xl.png);
    
        @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../image/3-about/about-bg_xl@2x.png);
        }
     }
}

.about__img{
    width: 280px;
    margin-bottom: 40px;

    @media screen and(min-width: 768px){
        width: 336px;
        margin-right: 32px;
        margin-bottom:0;
    }
    @media screen and(min-width: 1200px){
        width: 501px;
        margin-right: 99px;
    }
  
}

.about__description{
    display: block;
    position: relative;

    width: 280px;

    @media screen and(min-width: 768px){
        width: 334px;
    }
    @media screen and(min-width: 1200px){
        width: 463px;
    }

}

.overlay-second{
    width:100%;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    background-repeat: no-repeat;
    background-position: left 111px;
    background-size: contain;
    background-image: url(../image/3-about/about-bg_sm.png);
      
    // @media (min-device-pixel-ratio: 2),
    //     (-webkit-min-device-pixel-ratio: 2),
    //     (min-resolution: 192dpi),
    //     (min-resolution: 2dppx)  {
    //         background-image: url(../image/3-about/about-bg_sm@2x.png);
    // }
    @media screen and(min-width: 768px){
        background-image:none;
    }

}

.about__btn{
width: 176px;
height: 44px;
margin-bottom: 40px;

@media screen and(min-width: 768px){
    margin-bottom: 60px;
}
@media screen and(min-width: 1200px){
    margin-bottom: 81px;
}
}

.about__title-text{
    color:var(--color-title);
    font-weight: 500;
    font-size: 12px;
    line-height: 1.58;
    margin-bottom: 28px;
    @media screen and(max-width: 767px){
        width: 220px;
    }
    @media screen and(min-width: 768px){
        font-size: 14px;
        line-height: 1.76;
        margin-bottom: 23px;

    }
    @media screen and(min-width: 1200px){
        font-size: 16px;
        line-height: 1.75;
        letter-spacing: 0.04em;
        margin-bottom: 32px;
    }
    
}

.about__text{
    font-size: 12px;
    line-height: 1.58;
    color:var( --color-text);
    margin-bottom: 20px;

    @media screen and(max-width: 767px){
        width: 220px;
    }
    

@media screen and(min-width: 768px){
    font-size: 14px;
    line-height: 1.78;
    letter-spacing: 0.04em;
    margin-bottom: 40px;
}
@media screen and(min-width: 1200px){
    line-height: 1.93; 
}

}

