:root {
  --color-general: #d41443;
  --color-second: #ffa5ba;
  --btn-white: #fff;
  --btn-white-hover: #ecebeb;
  --color-title: #000;
  --color-text: #907e82;
  --anim-link: 250ms cubic-bezier(.1, .7, 1, .1);
  --font-general: "DM Sans";
  --font-second: "Titan One";
  --gap: 32px;
  --top: 20px;
  --card-number: 3;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, li, ul, ol {
  margin: 0;
  padding: 0;
}

input {
  border: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  border: none;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  font-family: var(--font-general);
  color: #fff;
  margin: 0;
  font-size: 16px;
}

body.no-scroll {
  overflow: hidden;
}

